.app-header_top-bar{height:25px;background-color:#546A7B}.app-header_bottom-bar{align-items:center;display:flex;flex-direction:row;justify-content:space-around;width:100%;height:80px;background-color:#87AEB5}.app-header_bottom-bar .container{align-items:center;display:flex;flex-direction:row;justify-content:space-between}.app-header_bottom-bar .container h1{display:inline-block;font-family:Quicksand,sans-serif;font-size:1.5em;font-stretch:condensed;font-weight:400;letter-spacing:0.1em;line-height:1.1;text-transform:uppercase;color:#393D3F}.app-header_bottom-bar .container a{min-width:80px;max-width:150px}@media (min-width: 960px){.app-header_bottom-bar .container{width:800px}}

.killer-container .killer-toolbar{align-items:center;display:flex;flex-direction:column;justify-content:space-between;margin:0 8px 32px;margin-bottom:32px}.killer-container .killer-toolbar>*:first-child{margin-bottom:16px}@media (min-width: 960px){.killer-container .killer-toolbar>*:first-child{margin-bottom:0}}@media (min-width: 960px){.killer-container .killer-toolbar{flex-direction:row;margin:0 0 32px}}

@-webkit-keyframes spin{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}@keyframes spin{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}@-webkit-keyframes rotate{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@keyframes rotate{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@-webkit-keyframes expandingLine{from,10%{stroke-dashoffset:0}50%,to{stroke-dashoffset:-112px}from,10%,to{stroke-dasharray:94px 19px}50%,60%{stroke-dasharray:.4px 112px}}@keyframes expandingLine{from,10%{stroke-dashoffset:0}50%,to{stroke-dashoffset:-112px}from,10%,to{stroke-dasharray:94px 19px}50%,60%{stroke-dasharray:.4px 112px}}@-webkit-keyframes colorMorphing{0%,35%,100%{stroke:#31313D}50%,85%{stroke:#919194}}@keyframes colorMorphing{0%,35%,100%{stroke:#31313D}50%,85%{stroke:#919194}}.loading-spinner{position:relative;margin:16px 0;width:40px;height:40px;font-size:40px}.loading-spinner-container{align-items:center;display:flex;flex-direction:column;justify-content:center;width:100%}.loading-spinner-front{position:absolute;top:0;overflow:visible;width:40px;height:40px;font-size:40px;fill:transparent;stroke-linecap:round;stroke-width:4px;-webkit-animation:2s rotate infinite linear;animation:2s rotate infinite linear}.loading-spinner-front circle{-webkit-animation:2s expandingLine infinite ease-in-out, 4s colorMorphing infinite linear;animation:2s expandingLine infinite ease-in-out, 4s colorMorphing infinite linear;stroke-dasharray:94px 19px}.loading-spinner-rear{position:absolute;top:0;overflow:visible;width:40px;height:40px;font-size:40px;fill:transparent;stroke-linecap:round;stroke-width:4px}.loading-spinner-rear circle{-webkit-animation:15s colorMorphing infinite linear;animation:15s colorMorphing infinite linear;stroke-opacity:0.15}

